.box-sizing,.message{box-sizing:border-box}.message{width:100%;position:fixed;bottom:0px;transition-property:opacity;transition-duration:2s;opacity:1}.message.message--fade-out{opacity:0}.message>*{text-align:center}.message .message--success{background-color:green}.message .message--error{background-color:red}.message .message--info{background-color:orange}
