.box-sizing,.tabs{box-sizing:border-box}.tabs{width:100%}.tabs .tabs-bar{width:100%}.tabs .tabs-bar .tab-header{display:inline-block;border-top:1px solid black;width:100px;cursor:pointer}.tabs .tabs-bar .tab-header.tab-header--disabled{cursor:default}.tabs .tabs-bar .tab-header ~ .tab-header{border-left:1px solid brown}.tabs .tabs-bar .tab-header:first-child{border-left:1px solid black}.tabs .tabs-bar .tab-header:last-child{border-right:1px solid black}.tabs .tabs-content .tab-body{display:none}.tabs .tabs-content .tab-body.tab-body--active{display:block}
